'use client'

import { useState } from 'react'
import { useRouter } from 'next/navigation'
import Link from 'next/link'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Textarea } from '@/components/ui/textarea'
import { Alert, AlertDescription } from '@/components/ui/alert'
import { motion } from 'framer-motion'

export default function RequestRidePage() {
  const [formData, setFormData] = useState({
    pickupAddress: '',
    dropoffAddress: '',
    scheduledTime: '',
    proposedPrice: '',
    notes: ''
  })
  const [loading, setLoading] = useState(false)
  const [error, setError] = useState('')
  const [success, setSuccess] = useState('')
  const router = useRouter()

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setLoading(true)
    setError('')
    setSuccess('')

    try {
      const token = localStorage.getItem('token')
      if (!token) {
        router.push('/customer/login')
        return
      }

      // Mock location coordinates (in real app, use geocoding API)
      const mockPickup = {
        lat: 40.7128,
        lng: -74.0060,
        address: formData.pickupAddress
      }

      const mockDropoff = {
        lat: 40.7589,
        lng: -73.9851,
        address: formData.dropoffAddress
      }

      const response = await fetch('/api/rides/create', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}`
        },
        body: JSON.stringify({
          pickupLocation: mockPickup,
          dropoffLocation: mockDropoff,
          scheduledTime: new Date(formData.scheduledTime).toISOString(),
          proposedPrice: parseFloat(formData.proposedPrice),
          notes: formData.notes
        })
      })

      const data = await response.json()

      if (data.success) {
        setSuccess('Ride request created successfully!')
        setTimeout(() => {
          router.push('/customer/dashboard')
        }, 2000)
      } else {
        setError(data.error || 'Failed to create ride request')
      }
    } catch (error) {
      console.error('Create ride error:', error)
      setError('Network error. Please try again.')
    } finally {
      setLoading(false)
    }
  }

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    setFormData(prev => ({
      ...prev,
      [e.target.name]: e.target.value
    }))
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-900 via-gray-800 to-gray-900">
      {/* Header */}
      <header className="bg-gray-800/50 backdrop-blur-sm border-b border-gray-700">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between items-center h-16">
            <div className="flex items-center space-x-4">
              <Link href="/" className="text-2xl font-bold text-white">
                Ride<span className="text-blue-400">Quote</span>
              </Link>
              <span className="text-gray-400">â€¢</span>
              <span className="text-gray-300">Request Ride</span>
            </div>
            <Button 
              variant="ghost" 
              onClick={() => router.push('/customer/dashboard')}
              className="text-gray-300 hover:text-white"
            >
              Back to Dashboard
            </Button>
          </div>
        </div>
      </header>

      <div className="max-w-3xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6 }}
        >
          <Card className="bg-gray-800/50 border-gray-700">
            <CardHeader>
              <CardTitle className="text-2xl text-white">Request a New Ride</CardTitle>
              <CardDescription className="text-gray-400">
                Set your own price and let drivers negotiate with you
              </CardDescription>
            </CardHeader>
            <CardContent>
              <form onSubmit={handleSubmit} className="space-y-6">
                {error && (
                  <Alert variant="destructive">
                    <AlertDescription>{error}</AlertDescription>
                  </Alert>
                )}
                
                {success && (
                  <Alert className="bg-green-500/20 border-green-500/50 text-green-400">
                    <AlertDescription>{success}</AlertDescription>
                  </Alert>
                )}

                <div className="space-y-2">
                  <Label htmlFor="pickupAddress" className="text-gray-300">Pickup Location</Label>
                  <Input
                    id="pickupAddress"
                    name="pickupAddress"
                    type="text"
                    placeholder="Enter pickup address"
                    value={formData.pickupAddress}
                    onChange={handleChange}
                    required
                    disabled={loading}
                    className="bg-gray-700 border-gray-600 text-white placeholder-gray-400"
                  />
                </div>

                <div className="space-y-2">
                  <Label htmlFor="dropoffAddress" className="text-gray-300">Dropoff Location</Label>
                  <Input
                    id="dropoffAddress"
                    name="dropoffAddress"
                    type="text"
                    placeholder="Enter dropoff address"
                    value={formData.dropoffAddress}
                    onChange={handleChange}
                    required
                    disabled={loading}
                    className="bg-gray-700 border-gray-600 text-white placeholder-gray-400"
                  />
                </div>

                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  <div className="space-y-2">
                    <Label htmlFor="scheduledTime" className="text-gray-300">Scheduled Time</Label>
                    <Input
                      id="scheduledTime"
                      name="scheduledTime"
                      type="datetime-local"
                      value={formData.scheduledTime}
                      onChange={handleChange}
                      required
                      disabled={loading}
                      className="bg-gray-700 border-gray-600 text-white"
                    />
                  </div>

                  <div className="space-y-2">
                    <Label htmlFor="proposedPrice" className="text-gray-300">Your Price ($)</Label>
                    <Input
                      id="proposedPrice"
                      name="proposedPrice"
                      type="number"
                      min="1"
                      step="0.01"
                      placeholder="Enter your proposed price"
                      value={formData.proposedPrice}
                      onChange={handleChange}
                      required
                      disabled={loading}
                      className="bg-gray-700 border-gray-600 text-white placeholder-gray-400"
                    />
                  </div>
                </div>

                <div className="space-y-2">
                  <Label htmlFor="notes" className="text-gray-300">Additional Notes (Optional)</Label>
                  <Textarea
                    id="notes"
                    name="notes"
                    placeholder="Any special requirements or preferences..."
                    value={formData.notes}
                    onChange={handleChange}
                    disabled={loading}
                    className="bg-gray-700 border-gray-600 text-white placeholder-gray-400"
                    rows={3}
                  />
                </div>

                <div className="bg-blue-500/10 border border-blue-500/20 rounded-lg p-4">
                  <p className="text-sm text-blue-300">
                    ðŸ’¡ <strong>Tip:</strong> Set a fair price based on distance and time. Drivers can accept or make counter-offers!
                  </p>
                </div>

                <Button 
                  type="submit" 
                  className="w-full bg-blue-500 hover:bg-blue-600 text-white"
                  disabled={loading}
                >
                  {loading ? 'Creating Request...' : 'Request Ride'}
                </Button>
              </form>
            </CardContent>
          </Card>
        </motion.div>
      </div>
    </div>
  )
}
